'use strict';

jQuery.fn.boxRollovers = function(option) {

  $(this).each(function() {

    var $elem = $(this);
    var $layer = $(this).find(option.inner);

    var XAngle = 0;
    var YAngle = 0;
    var Z = 50;

    $elem.on("mousemove touchmove",function(e){
      e.preventDefault();

      var XRel, YRel;

      if(typeof(e.touches) != 'undefined') {
        XRel = e.touches[0].pageX - $elem.offset().left;
        YRel = e.touches[0].pageY - $elem.offset().top;

      } else {
        XRel = e.pageX - $elem.offset().left;
        YRel = e.pageY - $elem.offset().top;

      }

      var width = $elem.width();

      YAngle = -(0.5 - (XRel / width)) * 20;
      XAngle = (0.5 - (YRel / width)) * 20;

      $elem.addClass('highlight');

      $layer.css({
          "transform":"translateZ(" + Z + "px) rotateX(" + XAngle + "deg) rotateY(" + YAngle + "deg)",
        });
    });

    $elem.on("mouseleave touchend",function(){

      $elem.removeClass('highlight');

      $layer.css({
          "transform":"translateZ(0) rotateX(0deg) rotateY(0deg)",
        });
    });
  })

  return $(this);
}
